.spinner {
  position: absolute;
  display: flex;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  &__content {
    border: 5px solid #eee;
    border-top: 5px solid #353535;
    border-radius: 50%;
    margin: auto;
    height: 50px;
    width: 50px;
    animation: spin 0.8s linear infinite;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
